/*
=============== 
Variables
===============
*/

:root {
  /* dark shades of primary color*/
  --clr-primary-1: hsl(205, 86%, 17%);
  --clr-primary-2: hsl(205, 77%, 27%);
  --clr-primary-3: hsl(205, 72%, 37%);
  --clr-primary-4: hsl(205, 63%, 48%);
  /* primary/main color */
  --clr-primary-5: hsl(205, 78%, 60%);
  /* lighter shades of primary color */
  --clr-primary-6: hsl(205, 89%, 70%);
  --clr-primary-7: hsl(205, 90%, 76%);
  --clr-primary-8: hsl(205, 86%, 81%);
  --clr-primary-9: hsl(205, 90%, 88%);
  --clr-primary-10: hsl(205, 100%, 96%);
  /* darkest grey - used for headings */
  --clr-grey-1: hsl(209, 61%, 16%);
  --clr-grey-2: hsl(211, 39%, 23%);
  --clr-grey-3: hsl(209, 34%, 30%);
  --clr-grey-4: hsl(209, 28%, 39%);
  /* grey used for paragraphs */
  --clr-grey-5: hsl(210, 22%, 49%);
  --clr-grey-6: hsl(209, 23%, 60%);
  --clr-grey-7: hsl(211, 27%, 70%);
  --clr-grey-8: hsl(210, 31%, 80%);
  --clr-grey-9: hsl(212, 33%, 89%);
  --clr-grey-10: hsl(210, 36%, 96%);
  --clr-white: #fff;
  --clr-red-dark: hsl(360, 67%, 44%);
  --clr-red-light: hsl(360, 71%, 66%);
  --clr-green-dark: hsl(125, 67%, 44%);
  --clr-green-light: hsl(125, 71%, 66%);
  --clr-black: #222;
  --transition: all 0.3s linear;
  --spacing: 0.1rem;
  --radius: 0.25rem;
  --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
  --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
  --max-width: 1170px;
  --fixed-width: 450px;
  --clr-orange-1: hsl(12, 83%, 98%);
  --clr-orange-2: hsl(14, 91%, 95%);
  --clr-orange-3: hsl(12, 89%, 89%);
  --clr-orange-4: hsl(13, 87%, 82%);
  --clr-orange-5: hsl(13, 88%, 68%);
  --clr-orange-6: hsl(13, 88%, 55%);
  --clr-orange-7: hsl(13, 74%, 49%);
  --clr-orange-8: hsl(13, 74%, 33%);
  --clr-orange-9: hsl(13, 73%, 25%);
  --clr-orange-10: hsl(13, 73%, 16%);
}
/*
=============== 
Global Styles
===============
*/

*,
::after,
::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  color: #222;
  line-height: 1.5;
  font-size: 0.875rem;
  background-image: url('https://images.unsplash.com/photo-1612036782180-6f0b6cd846fe?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=750&q=80');
  background-repeat: no-repeat;
  background-size: cover;
}
ul {
  list-style-type: none;
}
a {
  text-decoration: none;
}
h1,
h2,
h3,
h4 {
  letter-spacing: var(--spacing);
  text-transform: capitalize;
  line-height: 1.25;
  margin-bottom: 0.75rem;
}
h1 {
  font-size: 3rem;
}
h2 {
  font-size: 2rem;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 0.875rem;
}
p {
  margin-bottom: 1.25rem;
  color: var(--clr-grey-5);
}
@media screen and (min-width: 800px) {
  h1 {
    font-size: 4rem;
  }
  h2 {
    font-size: 2.5rem;
  }
  h3 {
    font-size: 1.75rem;
  }
  h4 {
    font-size: 1rem;
  }
  body {
    font-size: 1rem;
  }
  h1,
  h2,
  h3,
  h4 {
    line-height: 1;
  }
}
/*  global classes */

/* section */
.section,
.container {
  width: 90vw;
  margin: 0 auto;
  max-width: var(--max-width);
  background: #fff;
  opacity: 0.9;
  border-radius: 5px;
  padding-top: 20px;
  padding-bottom: 10px;
}

@media screen and (min-width: 700px) {
  .section {
    width: 95vw;
  }
}

.container {
  text-align: center;
  margin-top: 2rem;
}
.btn {
  display: inline-block;
  background: var(--clr-primary-5);
  color: var(--clr-white);
  padding: 0.25rem 0.75rem;
  border-radius: var(--radius);
  border-color: transparent;
  text-transform: capitalize;
  font-size: 1rem;
  letter-spacing: var(--spacing);
  margin-top: 2rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  cursor: pointer;
  transition: var(--transition);
}
.btn:hover {
  background: var(--clr-primary-1);
  color: var(--clr-primary-5);
}
.item {
  background: var(--clr-white);
  display: flex;
  justify-content: space-between;
  max-width: 800px;
  margin: 2rem auto;
  align-items: flex-start;
  border-radius: var(--radius);
}
.item button,
.item a {
  background: transparent;
  border-color: transparent;
  color: var(--clr-primary-5);
  letter-spacing: var(--spacing);
  cursor: pointer;
}
.item {
  padding: 1rem 2rem;
}
.item h4 {
  margin-bottom: 0;
  font-weight: bolder;
}
.item p {
  margin-bottom: 0;
}
.modal {
  position: absolute;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  background: var(--clr-white);
  display: inline-block;
  padding: 0.25rem 1rem;
  border-radius: var(--radius);
  text-transform: capitalize;
}

.modal p {
  margin-bottom: 0;
  color: var(--clr-red-dark);
}
.form {
  background: var(--clr-white);
  max-width: var(--fixed-width);
  margin: 0 auto;
  margin-bottom: 4rem;
  padding: 1rem 2rem;
  border-radius: var(--radius);
}
.form input {
  background: var(--clr-grey-10);
  border-color: transparent;
  border-radius: var(--radius);
  padding: 0.25rem 0.5rem;
}
.form-control {
  margin: 0.5rem 0;
  display: grid;
  grid-template-columns: 100px 1fr;
  align-items: center;
}
.form button {
  display: inline-block;
  background: var(--clr-black);
  color: var(--clr-white);
  border-color: transparent;
  margin-top: 1rem;
  letter-spacing: var(--spacing);
  padding: 0.15rem 0.25rem;
  text-transform: capitalize;
  border-radius: var(--radius);
  cursor: pointer;
}
.nav-links {
  max-width: var(--fixed-width);
  margin: 0 auto;
  margin-bottom: 4rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.nav-links a {
  color: var(--clr-grey-5);
}
.users {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}
.users li {
  width: 100%;
  display: flex;
  align-items: center;
  background: var(--clr-white);
  padding: 1rem 2rem;
  border-radius: var(--radius);
  text-align: left;
}
.users img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.users h4 {
  margin-bottom: 0.15rem;
}
.users a {
  color: var(--clr-grey-5);
  text-transform: capitalize;
}

.products {
  margin: 4rem 0;
  display: grid;
  gap: 2rem;
}
@media screen and (min-width: 576px) {
  .products {
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  }
}
.product {
  background: var(--clr-white);
  border-radius: var(--radius);
}
.product img {
  border-top-left-radius: var(--radius);
  border-top-right-radius: var(--radius);

  width: 100%;
  height: 15rem;
  object-fit: cover;
}
.product h4 {
  margin-top: 1rem;
}

.product button {
  margin-bottom: 1rem;
  background: var(--clr-primary-5);
  border-radius: var(--radius);
  border-color: transparent;
  color: var(--clr-white);
  padding: 0.25rem 0.5rem;
  text-transform: capitalize;
  cursor: pointer;
}

@media screen and (min-width: 620px) {
  #article{
    width: 500px;
  }
}
.underline{
  margin-left: auto;
  margin-right: auto;
  width: 5rem;
  height: .25rem;
  border-bottom: #2caeba 0.28rem solid;
}
div{
  display: block;
}
.outline{
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  background: #fff;
  text-align: center;
  padding: 1.5rem 2rem;
  border-radius: 0.25rem;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}
.outline:hover{
  box-shadow: 5px 10px 25px rgba(0,0,0,0.1);
  transition: .5s;
}
.image-container{
  width: 150px;
  height: 150px;
  margin-left: auto;
  margin-right: auto;
}
.image-container::before{
  position: absolute;
  border-radius: 50%;
  background: #49a6e9;
}
.img {
  width: 100%;
  display: block;
  height: 100%;
  object-fit: cover;
}
.image-container .img{
  position: relative;
  border-radius: 50%;
  border: 4px solid #bcccdc;
}
.quote{
  position: relative;
  z-index: 1;
  bottom: 130px;
  right: 60px;
  background: #49a6e9;
  padding: 6%;
  width: 1.25rem;
  height: 1rem;
  border-radius: 50%;
  color: #fff;
  object-fit: cover;
  transform: translateY(50%);
  place-items: center;
}
.left-btn{
  border: transparent;
  background-color: transparent;
  color: #49a6e9;
  margin-right: 1rem;
  font-size: 1.5rem;
  cursor: pointer;
  opacity: 0.7;
}
.left-btn:hover{
  color: #49a6e9;
  opacity: 1;
  transition: 0.5s;
}
.right-btn:hover{
  color: #49a6e9;
  opacity: 1;
  transition: 0.5s;
}
.left-btn:focus{
  background-color: transparent;
}
.right-btn:focus{
  background-color: transparent;
}
.right-btn{
  border: transparent;
  background-color: transparent;
  color: #49a6e9;
  margin-left: 1rem;
  font-size: 1.5rem;
  cursor: pointer;
  opacity: 0.7;
}
.btn-1{
  border: transparent;
  background-color: #ebf7ff;
  color: #49a6e9;
  margin-top: 1rem;
  padding: .5rem;
  border-radius: 5px;
  cursor: pointer;
}
.btn-1:hover{
  background-color: #49a6e9;
  color: #222;
  opacity: 0.9;
  transition: 0.5s;
}
.btn-1:target{
  background-color: #ebf7ff;
  color: #49a6e9;
}

.add-btn{
  color: #b4345c;
  background: hsl(210, 31%, 80%);;
  border-radius: 50%;
  width: 4%;
  height: 4%;
  opacity: 0.8;
  cursor: pointer;
}
.minus-btn{
  color: #b4345c;
  background: hsl(210, 31%, 80%);
  border-radius: 50%;
  width: 4%;
  height: 4%;
  opacity: 0.8;
  cursor: pointer;
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.question{
  margin-left: auto;
  margin-right: auto;
  border-radius: 5px;
  width: 50vw;
  border: 1px solid hsl(210, 31%, 80%);
  padding: 1rem 1.5rem;
  margin-bottom: 1rem;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}
.btn-container{
  margin-top: 1rem;
  display: flex;
  gap: 2rem;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.btn-cat{
  border: transparent;
  background: transparent;
  color: #c59d5f;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 20px;
  padding-right: 20px;
  font-size: large;
  font-weight: bold;
  text-transform: capitalize;
  cursor: pointer;
  border-radius: 5px;
  outline: none;
}
.btn-cat:focus{
  background: transparent;
  border:transparent;
  color: #c59d5f;
}
.btn-cat:hover{
  color: #fff;
  background-color: #e9b05b;
  transition: .5s;
}

.article-container{
  margin: 0 auto;
  max-width: 1170px;
  display: grid;
  grid-gap: 3rem 2rem;
  gap: 3rem 2rem;
  justify-items: center;
  margin-top:30px;
}
.article-item{
  display: flex;
}
.line{
  display:flex;
  flex-direction:row;
  justify-content:space-between;
  border-bottom: dotted 1px #222;
  padding: 0%;
}
.jobs-center{
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  gap: 10rem;
}
.company-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 4rem;
  flex-wrap: wrap;
  margin-top: 30px;
}
.company-btn{
  border: none;
  background: transparent;
  padding: 10px 50px;
  font-weight: bold;
  font-size: 20px;
  letter-spacing: .1rem;
}
.company-btn:hover{
