  }
  .company-btn {
    margin-bottom: 1rem;
  }
  .active-btn {
    box-shadow: -2px 0 var(--clr-primary-5);
  }
  .company-btn:hover {
    box-shadow: -2px 0 var(--clr-primary-5);
  }
}
.job-desc{
    display: grid;
    grid-template-columns: auto 1fr;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
    column-gap: 2rem;
    align-items: center;
    margin-bottom: 1.25rem;
}
.arr-btn{
  color: #2caeba;
}
.section-center{
  margin: 4rem auto 0;
  width: 80vw;
  height: 450px;
  max-width: 800px;
  position: relative;
  display: flex;
  align-items: center;
  overflow: hidden;
  text-align: center;
}
